@font-face {
  font-family: Sandoval,sans-serif;
  src: local('Sandoval'),url(".//.//fonts/Sandoval.woff2") format('woff2')
}
.fa {
  color: #fff
}
.fa-brands {
  color: #fff
}
.fa-instagram {
  color: #fff
}
.fa fa-enve .fa-brands:hover {
  color: red
}
.row3 {
  font-size: 1rem;
  padding: 15px
}
.fa:hover {
  color: red
}
html {
  scroll-behavior: smooth
}
section {
  text-align: justify
}
.col-6 {
  width: 100%;
  display: inline-table
}
footer {
  background-color: #171717;
  font-family: 'Alegreya Sans SC';
  border-top: 2px solid #fff;
  position: fixed;
  width: 100%;
  bottom: 0;
  height: 7.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

}
footer .logo {
  color: #fff;
  font-size: 28px
}
footer .row {
  display: inline-flex;
  color: #fff;
padding: 1rem;
padding-bottom: 1rem;

  flex-direction: column-reverse
} 
footer .row2 {
  display: flex;
  color: #fff;
  padding-right: 0;
  padding-left: 0;
  margin-right: 0;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: baseline;
  width: 50%
}
footer ul {
  padding: 0;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out
}
footer ul li {
  list-style-type: none;
  padding: 0;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  line-height: 2
}
.col-3 {
  display: inline-table;
  width: 25%
}
@media only screen and (max-width :1024px) {
  .col-3 {
    display: block;
    width: 100%
  }
  footer {
    position: fixed;
    width: 100%;
    background-color: #171717;
    bottom: 0;
    height: 3rem
  }
  /* footer .row {
   
    color: #fff;
    width: 50%;
    display: flex;
    justify-content: space-evenly
  } */
}
@media only screen and (min-width :768px) and (max-width :1024px) and (orientation :portrait) {
  footer {
    padding-right: 0;
    padding-left: 0
  }
  .col-3 {
    display: block;
    width: 100%;
    padding-bottom: 1em
  }
  footer {
    padding-right: 0;
    padding-left: 0;
    position: fixed;
    width: 100%
  }
}
@media only screen and (min-width :768px) and (max-width :1024px) and (orientation :landscape) {
  section {
    padding-bottom: 3em
  }
  .col-3 {
    display: block;
    width: 100%;
    padding-bottom: 1em
  }
  footer {
    font-family: 'Alegreya Sans SC';
    border-top: 2px solid #fff;
    position: fixed;
    width: 100%;
    bottom: 0;
    height: 4.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #171717;
    justify-content: space-between;
    padding-left: 1rem
  }
  /* footer .row {
    display: flex;
    color: #fff;
    width: 100%;
    justify-content: space-evenly
  } */
}
@media only screen and (max-width :600px) {
  .col-3 {
    display: block;
    width: 100%;
    padding-bottom: 1em
  }
  footer {
    position: fixed;
    width: 100%;
    height: 3rem;
    background-color: #171717
  }
  .col-6 {
    width: 100%;
    display: block
  }
}
@media only screen and (max-width :480px) {
  .col-6 {
    width: 100%;
    display: block
  }
  footer {
    position: fixed;
    width: 100%;
    display: flex;
    height: 7rem
  }
  footer .row {
    display: inline-flex;
    margin-top: 4rem;

  


  }
  footer .row2 {
    display: flex;

  margin-left: -1.2rem;
    color: #fff;

 
    width: 100%;
    
    letter-spacing: .6rem;
    line-height: 1rem
  }
}